.first-nav {
    font-size: size(sm);
    padding: 1rem;
    grid-column: 4/10;
    grid-row: 1/2;
    
    &-list{
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        list-style: none;
        align-items: center;
        height: 100%;
    }

    &-link{
        color: color(quaternary);
        text-decoration: none;
        font-weight: weight(lighter);

        &:hover{
            color: color(secondary);
        }
    }
}

.search-nav{
    grid-column: 1/4;
    grid-row: 2/3;
    display: flex;
    align-items: center;
    
    &-form {
        display: flex;
        gap: 15px;
    }

    &-button {
        background-color: color(secondary);
        color: color(tertiary);
        padding: 5px;
        font-size: size(xs);
        border: none;

        &:hover{
            background-color: color(quinary);
            cursor: pointer;
        }
    }

    &-input {
        font-size: size(xs);
        border: none;
        background-color: color(tertiary);
        border-bottom: 1px solid color(primary);

        &:focus{
            outline: none;
        }
    }
}