.form-nav {
    grid-column: 5/10;
    grid-row: 2/3;
    font-size: size(sm);

    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 15px;

    &-search {
        background-color: color(secondary);
        color: color(tertiary);
        padding: 5px;
        font-size: size(xs);
        border: none;

        &:hover{
            background-color: color(quinary);
            cursor: pointer;
        }
    }
}