.header{
    grid-column: 1 / -1;
    grid-row: 1 / 2;
    
    display: grid;
    grid-template-columns: minmax(10rem, 1fr) repeat(7, minmax(min-content, 16rem)) minmax(10rem, 1fr);
    grid-template-rows: repeat(3, min-content);
    grid-gap: 0.5rem;
    z-index: 100;
    padding: 15px;
    position: fixed;
    width: 100%;
    background-color: color(tertiary);
}