// Imports de fuentes
@import url('https://fonts.googleapis.com/css?family=Nunito:300,400,700');

// Paleta de colores
$colors:(
    primary: #1C1C1C,
    secondary: rgb(201, 164, 75),
    tertiary: #F8F5F2,
    quaternary: #9a9999,
    quinary: #2E4053
);

@function color($color-name){
    @return map-get($colors, $color-name);
}

// Tama√±os de fuentes
$font-sizes: (
    xxl: 6rem,
    xl: 3rem,
    lg: 2.5rem,
    md: 2rem,
    sm: 1.8rem,
    xs: 1.6rem
);

@function size($size){
    @return map-get($font-sizes, $size);
}

// Pesos de fuentes
$font-weights: (
    lighter: 100,
    light: 300,
    regular: 400,
    bold: 700
);

@function weight($weight){
    @return map-get($font-weights, $weight);
}

// Fuentes
$font-family: "Nunito", sans-serif;